import React from "react";

import Typewriter from "typewriter-effect";

function Hi(){
    return (<div>
        <Typewriter options={{loop:true}}
                onInit={(typewriter) => {
                    typewriter
                        .typeString("Hello").pauseFor(1000)
                        .deleteAll()
                        .typeString("नमस्ते").pauseFor(1000)
                        .deleteAll()
                        .typeString("Bonjour").pauseFor(1000)
                        .deleteAll()
                        .typeString("هتاف للترحيب").pauseFor(1000)
                        .deleteAll()
                        .typeString("안녕하세요").pauseFor(1000)
                        .deleteAll()
                        .typeString("こんにちは").pauseFor(1000)
                        .deleteAll()
                        .typeString("ನಮಸ್ಕಾರ").pauseFor(1000)
                        .deleteAll()

                        
                        
                        
                        .start();
                }}
 />
    </div>);
}

export default Hi;