import React from "react";

import Typewriter from "typewriter-effect";

function HelloType(){
    return (<div style={{paddingTop:`10vh`}}> 
        <Typewriter options={{loop:true}}
                onInit={(typewriter) => {
                    typewriter
                        .pasteString("Copyright (C) Ayush Nishad. All rights reserved.<br><br>PS C:\Users>")
                        .pauseFor(1000)
                        .typeString(" cd ayush")
                        .pasteString("<br>C:\Users/ayush>")
                        .pauseFor(1000)
                        .typeString(" Education")
                        .pauseFor(1000)
                        .pasteString("<br>BTech"+"<br>Computer science and Engineering")
                        .pasteString("<br>C:\Users/ayush>")
                        .pauseFor(2000)
                        .typeString(" Hackrank --profile")
                        .deleteChars(7)
                        .typeString("skills")
                        .pasteString("<br>C language :- ☆☆☆☆<br>CPP :- ☆☆☆☆☆<br>SQL :- ☆☆☆☆")
                        .pasteString("<br>C:\Users/ayush> ")
                        .pauseFor(1000)
                         .typeString(" leetcode --solution")
                         .pasteString("<br>100+ solved SQL+DSA")
                         .pasteString("<br>C:\Users/ayush>")
                         .pauseFor(1000)
                         .typeString(" certificate")
                         .pasteString("<br>Python Programming – E & ICT Academy<br>SQL (Intermediate) Verified<br>Goldman Sachs Engineering Virtual Program<br>Getting Started with Data Analytics on AWS<br>")
                          .pasteString("<br>C:\Users/ayush>")

                          .pauseFor(1000)
                        .typeString(" Experience")
                        .pauseFor(1000)
                        .pasteString("<br>loading...")
                        .pasteString("<br>C:\Users/ayush>")
                        .pauseFor(2000)
                        .pauseFor(40000)
                        .typeString(" clear")
                       
                        
                       
                        .start()
                }}
 />
    </div>);
}

export default HelloType;