import Carousel from 'react-bootstrap/Carousel';
import CPP from "./images/CPP.png"
import drumkit from "./images/drumkit.png"
import tindog from "./images/tindog.png"
import Button from 'react-bootstrap/Button';
import Kepper from "./images/Keeper.png"


function DarkVariantExample() {
  return (
    <div style={{backgroundColor:`black`,height:`5wv`}}>
    <h1 className='p'>Projects</h1>
    <Carousel style={{backgroundColor:`black`,marginTop:`5px`}} data-bs-theme="light">
   
      <Carousel.Item style={{padding:`10%`,paddingTop:`0px`,overflow:`hidden`}}>
        <a href='https://github.com/notayush/Cpp'><img 
          className="d-block w-100 imgZoom"
          src={CPP}
          alt="First slide"
        /></a>
        <Carousel.Caption >
       
          
          <p className='sm:text-3xl' >Fun Command-Line-Interface Game with Computer </p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item style={{padding:`10%`,paddingTop:`0px`}}>
      <a href='https://notayush.github.io/TinDog/'>  <img
          className="d-block w-100 imgZoom "
          src={tindog}
          alt="Second slide"
        /></a>
        <Carousel.Caption>
        
          
          <p className='sm:text-3xl' >Front end product landing Page, fully Responsive</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item style={{padding:`10%`,paddingTop:`0px`}}>
        <a href='https://notayush.github.io/DrumKIt/'> <img
          className="d-block w-100 imgZoom"
          src={drumkit}
          alt="Third slide"
        /></a>
        <Carousel.Caption>
       
          
          <p className='sm:text-3xl' >Be a Drummer not Dumber</p>
        </Carousel.Caption>
      </Carousel.Item>

      <Carousel.Item style={{padding:`15%`,paddingTop:`0px`}}>
         <a href='https://codesandbox.io/s/keeper-part-3-completed-forked-jp3n45?file=/src/components/Header.jsx'> <img
          className="d-block w-100 imgZoom"
          src={Kepper}
          alt="Third slide"
        /></a>
        <Carousel.Caption>
        
          
          <p className='sm:text-3xl '  >Put your all task in one place</p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
    </div>
  );
}

export default DarkVariantExample;